import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs';
import { SharedService } from '../../../../services/shared.service';
@Injectable({
  providedIn: 'root',
})
export class CollectionReportService {
  apiUrl = this.sharedService.getUrl;
  constructor(private http: HttpClient, public sharedService: SharedService) { }
  getCollectionsReport(params) {
    return this.http.get(
      this.apiUrl + `Treasury/CollectionsReport${params}`,
      this.sharedService.getHeaders()
    );
  }
  getRefundInvoicesReports(params) {
    return this.http.get(
      this.apiUrl + `Treasury/RefundInvoicesReports${params}`,
      this.sharedService.getHeaders()
    );
  }
  getRegisterPaymentTypes(id) {
    return this.http.get(
      this.apiUrl + `Register/GetRegisterPaymentTypes?registerId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  confirmReceivePettyCashAmount(formData): Observable<any> {
    return this.http.post<any>(
      this.apiUrl + 'PettyCash/ConfirmReceivePettyCashAmount',
      formData,
      this.sharedService.getHeaders()
    );
  }
  getServicesCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetServicesCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getDirectPaymentCollectionPaymentDetails(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetDirectPaymentCollectionPaymentDetails?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getSparePartsCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetSparePartsCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getOtherRevenueCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetOtherRevenueCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getCustomerPaymentCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetCustomerPaymentCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getPNOrderCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetPNOrderCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
  getTransferFeesCollectionPayments(id): Observable<any> {
    return this.http.get<any>(
      this.apiUrl + `Treasury/GetTransferFeesCollectionPayments?CollectionId=${id}`,
      this.sharedService.getHeaders()
    );
  }
}
