import { Component } from '@angular/core';
import { CollectionReportService } from '../../services/collection-report.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { SharedService } from '../../../../../services/shared.service';
import { PettyCashService } from '../../../cashier/petty-cash/services/petty-cash.service';
import { DatePipe, DecimalPipe } from '@angular/common';
import { CommonService } from '../../../../../services/common.service';
import { HeaderService } from '../../../../../services/header.service';
import * as XLSX from 'xlsx';
import { RefundDetailsComponent } from '../../../cashier/return/components/refund-details/refund-details.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable, Subject, catchError, debounceTime, forkJoin, of, takeUntil } from 'rxjs';
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';
import { ModalMessageComponent } from '../../../../../shared/components/modal-message/modal-message.component';
import { CollectService } from '../../../cashier/collect/services/collect.service';
import { OrderCollectionType } from '../../../../../shared/enums/enum';

@Component({
  selector: 'app-returned-invoice-report',
  templateUrl: './returned-invoice-report.component.html',
  styleUrls: ['./returned-invoice-report.component.scss']
})
export class ReturnedInvoiceReportComponent {
  destroy$ = new Subject<void>;
  filterForm: FormGroup;
  filterParams;
  pageNo = 1;
  totalRecords = 0;
  reportList = [];
  registerList = [];
  employeeList = [];
  paymentTypeList = [];
  isLoading = false;
  sort = 1;
  orderTypeList = [];
  invoiceTypeList = [{ text: 'Return Cash Invoice' }, { text: 'Return Credit Invoice' }, { text: 'Return Internal Invoice' }]
  constructor(
    private _collectionReport: CollectionReportService,
    private _sharedService: SharedService,
    public _headerService: HeaderService,
    private _pettyCashService: PettyCashService,
    private _modalService: NgbModal,
    private fb: FormBuilder,
    private _datepipe: DatePipe,
    private _currency: DecimalPipe,
    private _commonService: CommonService,
    private _collectService: CollectService,
  ) { }
  ngOnInit() {
    this.initFilterForm();
    this.getLookups();
    this.getRegisterPaymentTypes(this._sharedService.getRegister.registerObj.id);
    this.detectFilterFormChanges();
    //this.getCollectionsReport();
    this.filterForm.get('collectionDate').setValue([this._commonService.getMonthStartDate(), this._commonService.getMonthEndDate()]);
    //this._headerService.setTitle('Collections Report')
  }
  getLookups() {
    const register = this._pettyCashService.getRegistersList().pipe(catchError(error => of(error)));
    const employee = this._pettyCashService.getEmployees().pipe(catchError(error => of(error)));
    const orderType = this._pettyCashService.getLookupsById(7).pipe(catchError(error => of(error)));
    forkJoin([register, employee, orderType]).pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.registerList = data[0].data;
      this.employeeList = data[1].data;
      this.orderTypeList = data[2].data.map(x => {
        return { ...x, lookupName: x?.name[0]?.lookupName }
      }).filter(x => x.id == OrderCollectionType.ServicesSalesOrders
        || x.id == OrderCollectionType.SparePartsSalesOrders
        || x.id == OrderCollectionType.DirectPaymentSalesOrders);
    })
  }
  getDetails(details) {
    const modalRef = this._modalService.open(RefundDetailsComponent, { size: 'lg' });
    if (details.orderType == 7001)
      details.order.servicesSalesOrderNo = details.returnOrderId;
    else if (details.orderType == 7002)
      details.directPaymentSalesOrderOpportunityNo = details.returnOrderId;
    modalRef.componentInstance.data = { details, orderType: details?.orderType };
  }
  printInvoices(details) {
    if (details.orderType == 7001 || details.orderType == 7003) {
      this.getInvoice(details);
    }
    else {
      this._commonService.downloadInvoices({ invoices: details?.invoice })
    }
  }
  getInvoice(details) {
    let URL: Observable<any>;
    let orderNo = details?.returnOrderId;
    const formData = new FormData();
    if (details.orderType == 7001) {
      URL = this._collectService.viewServiceLayerReturnOrderOrigionalInvoice(formData);
      formData.append('returnOrderNo', orderNo);
    }
    else if (details.orderType == 7003) {
      URL = this._collectService.viewReturnOrderOrigionalInvoice(formData);
      formData.append('returnOrderNo', orderNo);
    }
    URL.subscribe({
      next: response => {
        if (response.isSuccess) {
          this._commonService.downloadURI(response.data);
        }
      },
      error: err => {
        this.responseModal('error', err?.error.errors || err?.error.Errors);
      }
    })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    if (type == 'success')
      ref.componentInstance.message = message;
    ref.componentInstance.messageError = message;
    ref.componentInstance.type = type;
  }
  detectFilterFormChanges() {
    this.filterForm.valueChanges.pipe(debounceTime(100), takeUntil(this.destroy$)).subscribe(value => {
      this.pageNo = 1;
      let orderDate = { orderDateFrom: '', orderDateTo: '' };
      let collectionDate = { refundedAtFrom: '', refundedAtTo: '' };
      let formValues;
      if (value['orderDate']) {
        orderDate['orderDateFrom'] = this._datepipe.transform(value['orderDate'][0], 'yyyy-MM-dd');
        orderDate['orderDateTo'] = this._datepipe.transform(value['orderDate'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...orderDate };
      delete formValues['orderDate'];
      if (value['collectionDate']) {
        collectionDate['refundedAtFrom'] = this._datepipe.transform(value['collectionDate'][0], 'yyyy-MM-dd');
        collectionDate['refundedAtTo'] = this._datepipe.transform(value['collectionDate'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...formValues, ...collectionDate };
      delete formValues['collectionDate'];
      if (formValues.registerId == null) {
        formValues = { ...formValues, registerId: '' }
      }
      if (formValues.paymentMethod == null) {
        formValues = { ...formValues, paymentMethod: '' }
      }
      if (formValues.serviceAdvisor == null) {
        formValues = { ...formValues, serviceAdvisor: '' }
      }
      if (formValues.refundedBy == null) {
        formValues = { ...formValues, refundedBy: '' }
      }
      if (formValues.invoiceType == null) {
        formValues = { ...formValues, invoiceType: '' }
      }
      if (formValues.orderTypeId == null) {
        formValues = { ...formValues, orderTypeId: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      this.getCollectionsReport(this.filterParams)
    })
  }
  initFilterForm() {
    this.filterForm = this.fb.group({
      registerId: [this._sharedService.getRegister.registerObj.id],
      registerSessionId: [''],
      orderNo: [''],
      customerName: [''],
      orderDate: [''],
      collectionDate: [''],
      paymentMethod: [null],
      serviceAdvisor: [null],
      refundedBy: [null],
      invoiceType: [null],
      orderTypeId: [null],
    })
  }
  clearControlValue(control) {
    this.filterForm.get(control).setValue('');
  }
  handleRegisterChange(event) {
    this.getRegisterPaymentTypes(event?.id);
    this.filterForm.get('paymentMethod').setValue(null);
  }
  resetDate(control) {
    if (control == "orderDate")
      this.filterForm.get(control).setValue('');
    else if (control == "collectionDate")
      this.filterForm.get(control).setValue('');
  }
  getCollectionsReport(filterParams = this.filterParams) {
    let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort}`
    this.isLoading = true;
    this.reportList = [];
    this._collectionReport.getRefundInvoicesReports((filterParams && filterParams + `&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort}`) || (`?${defaultParams}`))
      .pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
        this.reportList = response.data;
        this.totalRecords = response.totalRecordCount;
      }).add(() => {
        this.isLoading = false;
      })
  }
  //getCollectionsReportForExcel(filterParams = this.filterParams) {
  //  let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=0&pageSize=1000&sort=${this.sort}`
  //  this._collectionReport.getRefundInvoicesReports((filterParams && filterParams + `&pageNo=0&pageSize=1000&sort=${this.sort}`) || (`?${defaultParams}`))
  //    .subscribe((response: any) => {
  //      this.exportToExcel(response.data);
  //    });
  //}
  getCollectionsReportForExcel(from) {
    let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=0&pageSize=1000&sort=${this.sort}`
    this._collectionReport.getRefundInvoicesReports((this.filterParams && this.filterParams + `&pageNo=0&pageSize=1000&sort=${this.sort}`) || (`?${defaultParams}`))
      .pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
        if (from == 'excel')
          this.exportToExcel(response.data);
        else
          this.getCollectionsReportForPDF(response.data)
      });
  }
  getCollectionsReportForPDF(data) {
    let formValue = this.filterForm.value;
    const doc = new jsPDF({
      orientation: 'landscape'
    });
    const imageUrl = '/assets/images/pdfLogo.jpg';
    doc.addImage(imageUrl, 'jpg', 130, 5, 0, 0); // Parameters: (image URL, format, x, y, width, height)
    const registerName = this.registerList.find(x => x.id == formValue.registerId)?.registersName || '';
    const currentDate = new Date().toLocaleDateString();
    doc.addFont("/assets/images/Amiri-Regular.ttf", "Amiri", "normal");
    doc.setFontSize(12);
    const fontName = 'Amiri';
    doc.setFont(fontName, 'bold');
    doc.text(`Register Name : ${registerName}                    Date : ${currentDate}`, 10, 30);
    this.pdfTableData(doc, data)
  }
  pdfTableData(doc, dataList) {
    const headers = ['Serial', 'Account Number', 'Customer Name', 'Invoice Type', 'Invoice Number', 'Net', 'Tax', 'Refund', 'Copy'];
    let totalNetAmount = 0;
    let totalTaxAmount = 0;
    let totalRefundAmount = 0;
    const data = dataList.map((item, index) => {
      totalNetAmount += item?.order?.netTotal;
      totalTaxAmount += item?.order?.taxAmount;
      totalRefundAmount += item?.refundAmount;
      return [
        index + 1,
        item?.order?.customerId,
        item?.order?.customerName,
        item?.invoiceType,
        item?.orderInvoiceno,
        this.toDecimal(item?.order?.netTotal),
        this.toDecimal(item?.order?.taxAmount),
        this.toDecimal(item?.refundAmount),
        item?.invoice?.lastUsedViewSerial,
      ]
    })
    const totalRow = [
      'Total', '', '', '', '',
      this.toDecimal(totalNetAmount),
      this.toDecimal(totalTaxAmount),
      this.toDecimal(totalRefundAmount),
      ''
    ];
    const fontName = 'Amiri';
    autoTable(doc, {
      startY: 45,
      head: [[...headers]],
      body: [...data, totalRow],
      styles: { font: fontName, fontStyle: 'normal', fontSize: 10 },
      didParseCell: (data) => {
        if (data.row.index === data.table.body.length - 1) {
          data.cell.styles.fontSize = 12;
          data.cell.styles.textColor = [0, 0, 0];
        }
      },
    })
    doc.save('return-invoice-report.pdf');
  }
  toDecimal(value) {
    return this._currency.transform(value, '2.3-3');
  }
  exportToExcel(dataList): void {
    // Add headers to the data
    dataList = [...dataList, {}]; // added empty value due to add sum of some values at the end of last row in the exported excel file.
    let totalNetAmount = 0;
    let totalTaxAmount = 0;
    let totalCollectedAmount = 0;
    const data = dataList.map((item, index) => {
      if (Object.getOwnPropertyNames(item).length !== 0) {
        totalNetAmount += item?.order?.netTotal;
        totalTaxAmount += item?.order?.taxAmount;
        totalCollectedAmount += item?.refundAmount;
      }
      return {
        serialNo: index + 1,
        accountNumber: item?.order?.customerId,
        customerName: item?.order?.customerName,
        registersName: item?.register?.registersName,
        registerSessionId: item?.registerSessionId,
        serviceAdvisor: item?.serviceAdvisor,
        orderDate: this._datepipe.transform(item?.orderDate, 'mediumDate'),
        collectionAt: this._datepipe.transform(item?.refundAt, 'mediumDate'),
        orderNo: (item?.returnOrderId)?.toString(),
        invoiceType: item?.invoiceType,
        orderInvoiceSerial: (dataList?.length - 1) == index && 'Sum' || item?.orderInvoiceno,
        netTotal: (dataList?.length - 1) == index && totalNetAmount || (item?.order?.netTotal || '0'),
        taxAmount: (dataList?.length - 1) == index && totalTaxAmount || (item?.order?.taxAmount || '0'),
        collectedAmount: (dataList?.length - 1) == index && totalCollectedAmount || (this._currency.transform(item?.refundAmount, '2.3-3')),
        paymentMethod: item?.paymentMethod,
        copy: item?.invoice?.lastUsedViewSerial,
      }
    })
    var Heading = [
      ['#', 'Account Number', 'Customer Name', 'Register Name', 'Register Session Id', 'Sales Consultant',
        'Order Date', 'Refund At', 'Return Order Id', 'Inovice Type', 'Invoice Number', 'Net Amount (JOD)', 'Tax Amount (JOD)', 'Refund Amount (JOD)', ' Refund Payment Method', 'Copy']
    ];
    //Had to create a new workbook and then add the header
    const wb = XLSX.utils.book_new();
    const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet([]);
    XLSX.utils.sheet_add_aoa(ws, Heading);

    //Starting in the second row to avoid overriding and skipping headers
    XLSX.utils.sheet_add_json(ws, data, { origin: 'A2', skipHeader: true });

    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    XLSX.writeFile(wb, 'refund-report.xlsx');
  }
  getRegisterPaymentTypes(id) {
    this._collectionReport.getRegisterPaymentTypes(id).pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
      this.paymentTypeList = [];
      this.paymentTypeList = response.data.map(x => {
        return {
          name: x.translations[0].lookupName,
          id: x.paymentTypeId,
        }
      });
    })
  }
  pageChange(page) {
    this.pageNo = page;
    this.getCollectionsReport();
  }
  // Sorting Functions
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getCollectionsReport();
  }
  sortByRegister() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getCollectionsReport();
  }
  sortBySessionId() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getCollectionsReport();
  }
  sortByEmployee() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getCollectionsReport();
  }
  sortByOrderDate() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getCollectionsReport();
  }
  sortByCollectedDate() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getCollectionsReport();
  }
  sortByCollectedAmount() {
    if (this.sort == 15) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 14 ? 15 : 14;
    }
    this.getCollectionsReport();
  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
