import { Component, ViewChild } from '@angular/core';
import { CollectionReportService } from './services/collection-report.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { SharedService } from '../../../services/shared.service';
import { PettyCashService } from '../cashier/petty-cash/services/petty-cash.service';
import { DatePipe, DecimalPipe } from '@angular/common';
import { CommonService } from '../../../services/common.service';
import { HeaderService } from '../../../services/header.service';
import * as XLSX from 'xlsx';
import { Observable, Subject, Subscription, catchError, debounceTime, filter, forkJoin, of, takeUntil } from 'rxjs';
import { jsPDF } from 'jspdf';
import autoTable from 'jspdf-autotable'
import { CollectService } from '../cashier/collect/services/collect.service';
import { ModalMessageComponent } from '../../../shared/components/modal-message/modal-message.component';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { PaymentModalComponent } from '../cashier/collect/payment-modal/payment-modal.component';
import { NgxSpinnerService } from 'ngx-spinner';
import { OrderCollectionType, Status } from '../../../shared/enums/enum';
import { NavigationEnd, Router } from '@angular/router';
import { TabsetComponent } from 'ngx-bootstrap/tabs';
import { SubjectService } from '../../../services/subject.service';
@Component({
  selector: 'app-collection-report',
  templateUrl: './collection-report.component.html',
  styleUrls: ['./collection-report.component.scss'],
  providers: [DatePipe, DecimalPipe],
})
export class CollectionReportComponent {
  destroy$ = new Subject<void>;
  filterForm: FormGroup;
  filterParams;
  pageNo = 1;
  totalRecords = 0;
  reportList = [];
  registerList = [];
  employeeList = [];
  paymentTypeList = [];
  isLoading = false;
  sort = 1;
  orderTypeList = [];
  invoiceTypeList = [{ text: 'Cash Invoice' }, { text: 'Credit Invoice' }, { text: 'Internal Invoice' }];
  sessionStatus = Status;
  directPaymentDetails;
  previousUrl: string | null = null;
  subscriptions$ = new Subscription();
  activeTab = 0;
  @ViewChild(TabsetComponent, { static: true }) tabSet: TabsetComponent;
  sessionOpened = false;
  constructor(
    private _collectionReport: CollectionReportService,
    private _sharedService: SharedService,
    public _headerService: HeaderService,
    private _pettyCashService: PettyCashService,
    private fb: FormBuilder,
    private _datepipe: DatePipe,
    private _currency: DecimalPipe,
    private _commonService: CommonService,
    private _collectService: CollectService,
    private _modalService: NgbModal,
    private _spinner: NgxSpinnerService,
    private _subjectService: SubjectService,
    private _router: Router,
  ) {
    const navigation = this._router.getCurrentNavigation();
    if (navigation?.previousNavigation) {
      this.previousUrl = navigation.previousNavigation.finalUrl?.toString() || null;
    }
  }
  ngOnInit() {
    this.initFilterForm();
    this.getLookups();
    this.getRegisterPaymentTypes(this._sharedService.getRegister.registerObj.id);
    this.detectFilterFormChanges();
    this.filterForm.get('collectionDate').setValue([this._commonService.getMonthStartDate(), this._commonService.getMonthEndDate()]);
    this._headerService.setTitle('Collections and Return Invoices Report');
    this.activeTab = JSON.parse(localStorage.getItem('cr_filter'))?.tab || 0;
    if (this.activeTab >= 0) {
      this.tabSet.tabs[this.activeTab].active = true;
      localStorage.setItem('cr_filter', JSON.stringify({ ...JSON.parse(localStorage.getItem('cr_filter')), ...{ tab: this.activeTab } }))
    }
    this._subjectService.sessionDetails.pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.sessionOpened = data?.opened;
    })
  }
  getDirectPaymentSalesOrdersDetails$(opportunityNo) {
    return this._collectService.GetDirectPaymentSalesOrdersDetails(opportunityNo);
  }
  getServiceCenterCollectionPaymentDetails(item, value) {
    if (value == 'refund' && !this.sessionOpened) {
      this.responseModal('error', [{ errorMessageEn: 'You must have opened session for refund' }]);
      return;
    }
    this._spinner.show();
    let URL;
    let id = item?.orderCollectionId;
    if (item?.orderCollectionType == 7001)
      URL = this._collectionReport.getServicesCollectionPayments(id);
    else if (item?.orderCollectionType == 7002)
      URL = this._collectionReport.getDirectPaymentCollectionPaymentDetails(id);
    else if (item?.orderCollectionType == 7003)
      URL = this._collectionReport.getSparePartsCollectionPayments(id);
    else if (item?.orderCollectionType == 7004)
      URL = this._collectionReport.getOtherRevenueCollectionPayments(id);
    else if (item?.orderCollectionType == 7005)
      URL = this._collectionReport.getCustomerPaymentCollectionPayments(id);
    else if (item?.orderCollectionType == 7008)
      URL = this._collectionReport.getPNOrderCollectionPayments(id);
    else if (item?.orderCollectionType == 7009)
      URL = this._collectionReport.getTransferFeesCollectionPayments(id);
    URL.subscribe({
      next: res => {
        let data;
        if (item?.orderCollectionType == 7003)
          data = res.data[0];
        else
          data = res.data;
        this.handleReverse(item, data, value);
      }
    })
  }

  handleReverse(item, collectedOrderDetails, from) {
    let modalRef: NgbModalRef;
    let data = {
      reverse: from == 'reverse' ? true : false,
      refund: from == 'refund' ? true : false,
      collectionType: item?.orderCollectionType,
      orderCollectionId: item?.orderCollectionId,
      corrospondingSessionId: item?.registerSessionId,
      orderNo: item?.orderNo,
      invoiceType: item?.invoiceType,
      collectedAmount: item?.collectedAmount,
      customerName: item?.order?.customerName,
      collectedOrderDetails,
      fromCollectionReports: true,
    };
    if (item?.orderCollectionType == 7002) {
      //Below api is using because of getting flag prop from direct payment details because using this flag in paymentmodal
      this.getDirectPaymentSalesOrdersDetails$(collectedOrderDetails?.directPaymentSalesOrderOpportunityNo).pipe(takeUntil(this.destroy$)).subscribe({
        next: (response: any) => {
          this.directPaymentDetails = response.data;
          data['flag'] = this.directPaymentDetails?.flag;
          modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
          modalRef.componentInstance.detailsItem = data;
          this.subscribeToPaymnetModalEvent(modalRef);
        }
      }).add(() => this._spinner.hide());
    }
    else {
      modalRef = this._modalService.open(PaymentModalComponent, { modalDialogClass: 'payment-modal-size' });
      modalRef.componentInstance.detailsItem = data;
      this.subscribeToPaymnetModalEvent(modalRef);
      this._spinner.hide();
    }
  }
  private subscribeToPaymnetModalEvent(modalRef: NgbModalRef) {
    modalRef.componentInstance.sendtoLoadData.subscribe((result: any) => {
      this.getCollectionsReport();
    });
  }
  getDetails(type, id, orderId?) {
    if (type == 7001)
      this._commonService.NavigateToRoute(`/transactions-history/view-service-center/${id}`)
    else if (type == 7002)
      this._commonService.NavigateToRoute(`/transactions-history/view-direct-payment/${id}`)
    else if (type == 7003)
      this._commonService.NavigateToRoute(`/transactions-history/view-spare-part/${id}/${false}`)
    else if (type == 7004)
      this._commonService.NavigateToRouteWithQueryString(`transactions-history`, { queryParams: { type, id } })
    else if (type == 7005)
      this._commonService.NavigateToRouteWithQueryString(`transactions-history`, { queryParams: { type, id, oId: orderId } })
    else if (type == 7006)
      this._commonService.NavigateToRoute(`/transactions-history/view-undertaking/${id}`)
    else if (type == 7007)
      this._commonService.NavigateToRoute(`/transactions-history/view-finance/${id}`)
    else if (type == 7008)
      this._commonService.NavigateToRoute(`/transactions-history/view-promissory-note/${id}`)
    else if (type == 7009)
      this._commonService.NavigateToRouteWithQueryString(`transactions-history`, { queryParams: { type, id, oId: orderId } })
  }
  printInvoices(item) {
    if (item?.orderCollectionType == 7001 || item?.orderCollectionType == 7003) {
      this.getInvoice(item);
    }
    else {
      this._commonService.downloadInvoices({ invoices: item?.invoice?.copy })
    }
  }
  getInvoice(item) {
    let URL: Observable<any>;
    let orderNo = '';
    const formData = new FormData();
    orderNo = item?.orderNo;
    formData.append('OrderNo', orderNo);
    if (item?.orderCollectionType == 7001)
      URL = this._collectService.viewOrderOrigionalInvoiceServiceOrder(formData);
    else if (item?.orderCollectionType == 7003)
      URL = this._collectService.viewOrderOrigionalInvoice(formData);
    URL.subscribe({
      next: response => {
        if (response.isSuccess) {
          this._commonService.downloadURI(response.data);
        }
      },
      error: err => {
        this.responseModal('error', err?.error.errors || err?.error.Errors);
      }
    })
  }
  responseModal(type, message) {
    const ref = this._modalService.open(ModalMessageComponent);
    if (type == 'success')
      ref.componentInstance.message = message;
    ref.componentInstance.messageError = message;
    ref.componentInstance.type = type;
  }
  handleTabSelection(e, tabId) {
    this.activeTab = tabId;
  }
  detectFilterFormChanges() {
    this.filterForm.valueChanges.pipe(debounceTime(1000), takeUntil(this.destroy$)).subscribe(value => {
      this.pageNo = 1;
      let orderDate = { orderDateFrom: '', orderDateTo: '' };
      let collectionDate = { collectedAtFrom: '', collectedAtTo: '' };
      let formValues;
      if (value['orderDate']) {
        orderDate['orderDateFrom'] = this._datepipe.transform(value['orderDate'][0], 'yyyy-MM-dd');
        orderDate['orderDateTo'] = this._datepipe.transform(value['orderDate'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...value, ...orderDate };
      delete formValues['orderDate'];
      if (value['collectionDate']) {
        collectionDate['collectedAtFrom'] = this._datepipe.transform(value['collectionDate'][0], 'yyyy-MM-dd');
        collectionDate['collectedAtTo'] = this._datepipe.transform(value['collectionDate'][1], 'yyyy-MM-dd');;
      }
      formValues = { ...formValues, ...collectionDate };
      delete formValues['collectionDate'];
      if (formValues.registerId == null) {
        formValues = { ...formValues, registerId: '' }
      }
      if (formValues.paymentMethod == null) {
        formValues = { ...formValues, paymentMethod: '' }
      }
      if (formValues.serviceAdvisor == null) {
        formValues = { ...formValues, serviceAdvisor: '' }
      }
      if (formValues.collectedBy == null) {
        formValues = { ...formValues, collectedBy: '' }
      }
      if (formValues.invoiceType == null) {
        formValues = { ...formValues, invoiceType: '' }
      }
      if (formValues.orderTypeId == null) {
        formValues = { ...formValues, orderTypeId: '' }
      }
      this.filterParams = '?' + new URLSearchParams(formValues).toString();
      this.getCollectionsReport(this.filterParams)
    })
  }
  initFilterForm() {
    this.filterForm = this.fb.group({
      registerId: [this._sharedService.getRegister.registerObj.id],
      registerSessionId: [''],
      orderNo: [''],
      //orderNo: ['100288077'],
      //orderNo: ['1873'], //Cash Only
      //orderNo: ['2408113'], // All Pyament types
      customerName: [''],
      orderDate: [''],
      collectionDate: [''],
      paymentMethod: [null],
      serviceAdvisor: [null],
      collectedBy: [null],
      invoiceType: [null],
      orderTypeId: [null],
      //orderTypeId: [7001],
    })
    if (this.previousUrl?.includes('/transactions-history')) {
      let filters = JSON.parse(localStorage.getItem('cr_filter'));
      const orderDate = filters?.orderDate && [new Date(filters?.orderDate[0]), new Date(filters?.orderDate[1])]
      const collectionDate = filters?.collectionDate && [new Date(filters?.collectionDate[0]), new Date(filters?.collectionDate[1])];
      filters = { ...filters, orderDate, collectionDate };
      this.filterForm.patchValue(filters);
    }
  }
  clearControlValue(control) {
    this.filterForm.get(control).setValue('');
  }
  handleRegisterChange(event) {
    this.getRegisterPaymentTypes(event?.id);
    this.filterForm.get('paymentMethod').setValue(null);
  }
  resetDate(control) {
    if (control == "orderDate")
      this.filterForm.get(control).setValue('');
    else if (control == "collectionDate")
      this.filterForm.get(control).setValue('');
  }
  getCollectionsReport(filterParams = this.filterParams) {
    let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort}`
    this.isLoading = true;
    this.reportList = [];
    this._collectionReport.getCollectionsReport((filterParams && filterParams + `&pageNo=${this.pageNo - 1}&pageSize=6&sort=${this.sort}`) || (`?${defaultParams}`))
      .pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
        this.reportList = response.data.map(x => {
          let id = x?.registerSessionStatus?.lookupId;
          return {
            ...x,
            reverseable: x?.canReverse,
            refundable: (x?.orderCollectionType !== OrderCollectionType.ServicesSalesOrders &&
              x?.orderCollectionType !== OrderCollectionType.SparePartsSalesOrders)
              && x?.canRefunded
          }
        });
        this.totalRecords = response.totalRecordCount;
      }).add(() => {
        this.isLoading = false;
      })
  }
  getCollectionsReportForExcel(from) {
    let defaultParams = `registerId=${this._sharedService.getRegister.registerObj.id}&pageNo=0&pageSize=1000&sort=${this.sort}`
    this._collectionReport.getCollectionsReport((this.filterParams && this.filterParams + `&pageNo=0&pageSize=1000&sort=${this.sort}`) || (`?${defaultParams}`))
      .pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
        if (from == 'excel')
          this.exportToExcel(response.data);
        else
          this.getCollectionsReportForPDF(response.data)
      });
  }
  getCollectionsReportForPDF(data) {
    let formValue = this.filterForm.value;
    const doc = new jsPDF({
      orientation: 'landscape'
    });
    const imageUrl = '/assets/images/pdfLogo.jpg';
    doc.addImage(imageUrl, 'jpg', 130, 5, 0, 0); // Parameters: (image URL, format, x, y, width, height)
    const registerName = this.registerList.find(x => x.id == formValue.registerId)?.registersName || '';
    const currentDate = new Date().toLocaleDateString();
    doc.addFont("/assets/images/Amiri-Regular.ttf", "Amiri", "normal");
    doc.setFontSize(12);
    const fontName = 'Amiri';
    doc.setFont(fontName, 'bold');
    doc.text(`Register Name : ${registerName}                    Date : ${currentDate}`, 10, 30);
    this.pdfTableData(doc, data)
  }
  pdfTableData(doc, dataList) {
    const headers = ['Serial', 'Account Number', 'Customer Name', 'Invoice Type', 'Invoice Serial', 'Net', 'Tax', 'Collected', 'Copy'];
    let totalNetAmount = 0;
    let totalTaxAmount = 0;
    let totalCollectedAmount = 0;
    const data = dataList.map((item, index) => {
      totalNetAmount += item?.order?.netTotal;
      totalTaxAmount += item?.order?.taxAmount;
      totalCollectedAmount += item?.collectedAmount;
      return [
        index + 1,
        item?.order?.customerId,
        item?.order?.customerName,
        item?.invoiceType,
        item?.orderInvoiceSerial,
        this.toDecimal(item?.order?.netTotal),
        this.toDecimal(item?.order?.taxAmount),
        this.toDecimal(item?.collectedAmount),
        item?.invoice?.lastUsedViewSerial,
      ]
    })
    const totalRow = [
      'Total', '', '', '', '',
      this.toDecimal(totalNetAmount),
      this.toDecimal(totalTaxAmount),
      this.toDecimal(totalCollectedAmount),
      ''
    ];
    const fontName = 'Amiri';
    autoTable(doc, {
      startY: 35,
      head: [[...headers]],
      body: [...data, totalRow],
      styles: { font: fontName, fontSize: 10, },
      didParseCell: (data) => {
        if (data.row.index === data.table.body.length - 1) {
          data.cell.styles.fontSize = 12;
          data.cell.styles.textColor = [0, 0, 0];
        }
      },
    })
    doc.save('collection-report.pdf');
  }
  toDecimal(value) {
    return this._currency.transform(value, '2.3-3');
  }
  exportToExcel(dataList): void {
    // Add headers to the data
    dataList = [...dataList, {}]; // added empty value due to add sum of some values at the end of last row in the exported excel file.
    let totalNetAmount = 0;
    let totalTaxAmount = 0;
    let totalCollectedAmount = 0;
    const data = dataList.map((item, index) => {
      if (Object.getOwnPropertyNames(item).length !== 0) {
        totalNetAmount += item?.order?.netTotal;
        totalTaxAmount += item?.order?.taxAmount;
        totalCollectedAmount += item?.collectedAmount;
      }
      return {
        serialNo: index + 1,
        accountNumber: item?.order?.customerId,
        customerName: item?.order?.customerName,
        registersName: item?.register?.registersName,
        registerSessionId: item?.registerSessionId,
        serviceAdvisor: item?.serviceAdvisor,
        orderDate: this._datepipe.transform(item?.orderDate, 'mediumDate'),
        collectionAt: this._datepipe.transform(item?.collectionAt, 'mediumDate'),
        orderNo: (item?.orderNo)?.toString(),
        invoiceType: item?.invoiceType,
        orderInvoiceSerial: (dataList?.length - 1) == index && 'Sum' || item?.orderInvoiceSerial,
        netTotal: (dataList?.length - 1) == index && totalNetAmount || (item?.order?.netTotal || '0'),
        taxAmount: (dataList?.length - 1) == index && totalTaxAmount || (item?.order?.taxAmount || '0'),
        collectedAmount: (dataList?.length - 1) == index && totalCollectedAmount || (this._currency.transform(item?.collectedAmount, '2.3-3')),
        paymentMethod: item?.paymentMethod,
        copy: item?.invoice?.lastUsedViewSerial,
      }
    })
    var Heading = [
      ['#', 'Account Number', 'Customer Name', 'Register Name', 'Register Session Id', 'Sales Consultant',
        'Order Date', 'Collection At', 'Order No', 'Inovice Type', 'Invoice Number', 'Net Amount (JOD)', 'Tax Amount (JOD)', 'Collected Amount (JOD)', 'Payment Method', 'Copy']
    ];
    //Had to create a new workbook and then add the header
    const wb = XLSX.utils.book_new();
    const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet([]);
    XLSX.utils.sheet_add_aoa(ws, Heading);

    //Starting in the second row to avoid overriding and skipping headers
    XLSX.utils.sheet_add_json(ws, data, { origin: 'A2', skipHeader: true });

    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    XLSX.writeFile(wb, 'collection-report.xlsx');
  }
  getLookups() {
    const register = this._pettyCashService.getRegistersList().pipe(catchError(error => of(error)));
    const employee = this._pettyCashService.getEmployees().pipe(catchError(error => of(error)));
    const orderType = this._pettyCashService.getLookupsById(7).pipe(catchError(error => of(error)));
    forkJoin([register, employee, orderType]).pipe(takeUntil(this.destroy$)).subscribe(data => {
      this.registerList = data[0].data;
      this.employeeList = data[1].data;
      this.orderTypeList = data[2].data.map(x => {
        return {
          ...x,
          lookupName: x?.name[0]?.lookupName
        }
      });
    })
  }
  getRegisterPaymentTypes(id) {
    this._collectionReport.getRegisterPaymentTypes(id).pipe(takeUntil(this.destroy$)).subscribe((response: any) => {
      this.paymentTypeList = [];
      this.paymentTypeList = response.data.map(x => {
        return {
          name: x.translations[0].lookupName,
          id: x.paymentTypeId,
        }
      });
    })
  }
  pageChange(page) {
    this.pageNo = page;
    this.getCollectionsReport();
  }
  // Sorting Functions
  sortByCustomer() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getCollectionsReport();
  }
  sortByRegister() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getCollectionsReport();
  }
  sortBySessionId() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getCollectionsReport();
  }
  sortByEmployee() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getCollectionsReport();
  }
  sortByOrderDate() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getCollectionsReport();
  }
  sortByCollectedDate() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getCollectionsReport();
  }
  sortByCollectedAmount() {
    if (this.sort == 15) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 14 ? 15 : 14;
    }
    this.getCollectionsReport();
  }
  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
    this.subscriptions$.unsubscribe();
    let nextRoute = this._router.url;
    if (nextRoute.includes('/transactions-history')) {
      const filters = { ...this.filterForm.value, tab: this.activeTab };
      localStorage.setItem('cr_filter', JSON.stringify(filters));
    }
    else
      localStorage.removeItem('cr_filter');

  }
}
